import React from "react";
import { Input, Button } from "theme-ui";

const UrlTransfer = () => {
  const [url, setUrl] = React.useState("");
  const onClick = () => {
    if (url.indexOf("github.com") !== -1) {
      alert(url.replace("github.com", "cdn.jsdelivr.net/gh"));
    } else {
      alert("invalid github url");
    }
  };
  return (
    <>
      <Input
        value={url}
        onChange={(e) => setUrl(e.target.value)}
        style={{ display:"inline", width: "calc(100% - 5em)", marginRight: "1em" }}
      />
      <Button onClick={onClick}>转换</Button>
    </>
  );
};

export default UrlTransfer;
